<template>
  <div id="addparameter">
    <table cellpadding="10">
      <tr v-for="(param, idx) in params"
          :key="idx">
        <th v-if="idx < 4">
          {{param.title}}
        </th>
        <th v-else>
          <el-button type="danger"
                     class="remove-one"
                     @click="removeOne(idx)">Del</el-button>
        </th>
        <td>
          <input type="text"
                 class="arrKey"
                 placeholder="参数索引"
                 v-model="param.label"
                 :name="param.value"
                 @input="passParams">
        </td>
        <td v-if="idx < 4 ">
          <input type="text"
                 class="arrVal"
                 disabled
                 :value="param.variable">
        </td>
        <td v-else>
          <textarea class="arrVal"
                    :name="param.comment"
                    id=''
                    cols='23'
                    rows='3'
                    v-model="param.variable"></textarea>
        </td>
      </tr>
      <tr>
        <th style="color:#C73E3A">
          輸入新參數 →
        </th>
        <td>
          <input type="text"
                 placeholder="参数索引"
                 id="paramKey"
                 autocomplete="off">
        </td>
        <td>
          <input type="text"
                 placeholder="参数值"
                 @keyup.enter="addOne"
                 id="paramValue"
                 autocomplete="off">
        </td>
      </tr>
    </table>
    <el-button type="primary"
               id="addOne"
               @click="addOne">+</el-button>
  </div>
</template>
<script>
export default {
  name: 'addparameter',
  data() {
    return {
      params: [
        {
          title: '商户号',
          label: '',
          variable: '$pay_mid',
          value: 'pay_mid',
          key: 1,
        },
        {
          title: '商户订单号',
          label: '',
          variable: '$order_no',
          value: 'order_no',
          key: 2,
        },
        {
          title: '订单金额',
          label: '',
          variable: '$MOAmount',
          value: 'MOAmount',
          key: 3,
        },
        {
          title: '异步通知URL',
          label: '',
          variable: '$notify_url',
          value: 'notify_url',
          key: 4,
        },
      ],
    };
  },
  methods: {
    addOne() {
      let paramInfo = document.querySelector('#paramInfo');
      let paramKey = document.querySelector('#paramKey');
      let paramValue = document.querySelector('#paramValue');
      if (paramKey.value == '' || paramValue.value == '') {
        alert('请填入参数索引/参数值');
      } else {
        let newParam = {
          title: 'none',
          label: paramKey.value,
          variable: paramValue.value,
          value: `append${this.params.length + 1}`,
          comment: `comment${this.params.length + 1}`,
          key: this.params.length + 1,
        };
        this.params.push(newParam);
        paramKey.value = '';
        paramValue.value = '';
        paramKey.focus();
        this.passParams();
      }
    },
    removeOne(idx) {
      this.params.splice(idx, 1);
      this.passParams();
    },
    passParams() {
      this.$bus.$emit('pass', this.params);
    },
  },
};
</script>
<style lang="scss" scoped>
#addparameter {
  box-sizing: border-box;
  position: relative;
  padding-bottom: 15px;
  padding-top: 15px;
  border-bottom: 1px solid #707c74;
}

#addparameter table {
  width: 90%;
  text-align: center;
}

#addparameter table td {
  margin-bottom: 50px;
}

#addparameter #addOne {
  position: absolute;
  right: 6%;
  bottom: 24px;
}

#addparameter button.remove-one {
  display: block;
  margin: 0 auto;
}
</style>


